import React from "react";
import styles from './index.module.scss'
import {formApi} from "../../api/form";

class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            email: '',
            companyName: '',
            textarea: '',
            isName: false,
            isEmail: false
        }
    }
    onChangeInfo = (value, input) => {
        switch (input) {
            case 'name':
                this.setState({name: value, isName: false});
                break;
            case 'email':
                this.setState({email: value, isEmail: false});
                break;
            case 'company':
                this.setState({companyName: value});
                break;
            case 'textarea':
                this.setState({textarea: value});
                break;
            default:
                  return null;
        }
    };
    onSubmit = () => {
        if(this.state.name === '' && this.state.email === ''){
            this.setState({
                isName: true,
                isEmail: true
            })
        }
        else if(this.state.name === ''){
            this.setState({
                isName: true
            })
        }
        else if(this.state.email === ''){
            this.setState({
                isEmail: true
            })
        }else{
            const params ={
                personName: this.state.name,
                personEmail: this.state.email,
                companyName: this.state.companyName,
                info: this.state.textarea,
            };
            formApi(params)
              .then(res => console.log(res));
        }
    };

    render() {
        return (
          <footer className={styles.footer}>
              <a href={'!#'} name="contacts"> </a>
              <div className={styles.footer_block}>
                  <div className={styles.form_block}>
                      <div className={styles.caption}>Contact us:</div>
                      <div className={this.state.isName ? `${styles.input_block} ${styles.name} ${styles.border_red}` : `${styles.input_block} ${styles.name}`}>
                          <input className={styles.input} type="text" placeholder="Your Name"
                                 onChange={(event) => this.onChangeInfo(event.target.value, 'name')}/>
                      </div>
                      <div className={this.state.isEmail ? `${styles.input_block} ${styles.email} ${styles.border_red}` : `${styles.input_block} ${styles.email}`}>
                          <input className={styles.input} type="email" placeholder="E-mail"
                                 onChange={(event) => this.onChangeInfo(event.target.value, 'email')}/>
                      </div>
                      <div className={`${styles.input_block} ${styles.company}`}>
                          <input className={styles.input} type="text" placeholder="Company Name"
                                 onChange={(event) => this.onChangeInfo(event.target.value, 'company')}/>
                      </div>
                      <div className={styles.textarea_block}>
                          <textarea className={styles.textarea_text} placeholder="Your message"
                                    onChange={(event) => this.onChangeInfo(event.target.value, 'textarea')}>
                          </textarea>
                      </div>
                      <div className={styles.button} onClick={this.onSubmit}>Send message</div>
                  </div>
                  <div className={styles.footer_info}>Â© 2020 Crypto Pay. Trademarks belong to their respective owners.
                      All rights reserved</div>
              </div>
          </footer>
        )
    }
}
export default Footer;